import * as Yup from 'yup';

export const SchemaFormStep1 = Yup.object().shape({
    username: Yup.string().required('    ')
        .matches(
            (/^[a-z0-9]+$/i),
            '    ',)
            .matches((/[a-zA-Z]/), '')

            ,
    password: Yup.string()
        .required('    ')
        .min(8, '    8 ')
        .max(16)
        .matches(
            /(?=.*[A-Z])\w+/,
            '      ',
        )
        .matches(/\d/, '      '),
});



export const SchemaStep2 = Yup.object().shape({
    firstName: Yup.string().required('    ')
        .matches(/^\S*$/, ' ')
        .max(16),
    lastName: Yup.string()
        .required('    ')
        .matches(/^\S*$/, ' ')
        .max(25),

});

export const SchemaLastStep = Yup.object().shape({
    phone: Yup.string().required('    ')
     .matches(/(?:\+375)\s?\(?29|25|33|44\)?\s?\d\d(?:\d[-\s]\d\d[-\s]\d\d|[-\s]\d\d[-\s]\d\d\d|\d{5})/, '  +375 (xx) xxx-xx-xx')
     .matches(/^([^\\s*]+)/g,'poiuyt')
     .matches(/(.*\d.*){12}/, '  +375 (xx) xxx-xx-xx')
     ,
     email: Yup.string().required('    ')
     .matches(/^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/, '  e-mail'),
 });


 export const SchemaSignIn = Yup.object().shape({
    identifier: Yup.string().required('    ')
    ,
    password: Yup.string()
        .required('    ')

});